<template>
  <div ref="testimonialContainer" class="testimonial-container">
    <div class="testimonial-top-container">
      <p class="testimonial-header">See the Results!</p>
      <p class="testimonial-top-text">Real Transformations with Our Coating</p>
    </div>
    <div class="testimonial-bottom-container">
      <div class="testimonial-card-container">
        <img v-for="(card, index) in visibleArray" :key="index" class="testimonial-card" :src="card.data.imageUrl" alt="img" @click="showModal(card.data.imageUrl)">
      </div>
      <div class="testimonial-button-container">
        <button class="testimonial-prev-button" @click="showPrevCards"></button>
        <button class="testimonial-next-button" @click="showNextCards"></button>
      </div>
    </div>
  </div>
  <ImageModal
      v-if="this.selectedTestimonial"
      :image-url="selectedTestimonial"
      @closeModal="closeModal"/>
</template>
<script>

import ImageModal from "@/components/modal/image_modal/ImageModal.vue";

function testimonialCard(imageUrl) {
  return {
    data : {
      imageUrl: imageUrl,
    }
  }
}

export default {
  name: "TestimonialPage",
  components: {
    ImageModal,
  },
  data () {
    return {
      testimonials : [
        testimonialCard("https://i.imgur.com/SYLvbfd.jpeg"),
        testimonialCard("https://i.imgur.com/B06esZ0.jpeg"),
        testimonialCard("https://i.imgur.com/IPjh2ys.jpeg"),
        testimonialCard("https://i.imgur.com/aDgScma.jpeg"),
        testimonialCard("https://i.imgur.com/wi0dcWc.jpeg"),

      ],
      visibleArray: [],
      startIndex: 0,
      cardUpdated: false,
      intervalId: null,
      selectedTestimonial: null
    }
  },
  mounted() {
    if (window.innerWidth < 768) {
      this.cardUpdated = true
      this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 1)
      this.intervalId = setInterval(() => {
        this.startIndex = (this.startIndex + 1) % this.testimonials.length
        this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 1)
      }, 5000);
    } else {
      this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 3)
      this.intervalId = setInterval(() => {
        this.startIndex++
        if (this.startIndex === 3) {
          this.startIndex = 0
        }
        this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 3)
      }, 5000);
    }
    this.setInitialHeight()
    window.addEventListener('resize', this.setVisibleArray)
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.setVisibleArray)
  },
  methods: {
    showNextCards() {
      clearInterval(this.intervalId);
      if (window.innerWidth < 768 && this.startIndex !== this.testimonials.length - 1) {
        this.startIndex++
        this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 1)
      } else {
        if (this.startIndex + 3 < this.testimonials.length) {
          this.startIndex++
          this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 3)
        }
      }
    },
    showPrevCards() {
      clearInterval(this.intervalId);
      if (window.innerWidth < 768 && this.startIndex !== 0) {
        this.startIndex--
        this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 1)
      } else {
        if (this.startIndex !== 0) {
          this.startIndex--
          this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 3)
        }
      }
    },
    setInitialHeight() {
      const originalHeight = window.innerHeight + 'px'
      const testimonialContainer = this.$refs.testimonialContainer

      testimonialContainer.style.maxHeight = originalHeight
    },
    setVisibleArray() {
      if (window.innerWidth < 768 && this.cardUpdated === false) {
        clearInterval(this.intervalId)
        console.log("masuk")
        this.cardUpdated = true
        this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 1)
        this.intervalId = setInterval(() => {
          this.startIndex = (this.startIndex + 1) % this.testimonials.length
          this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 1)
        }, 5000);
      } else if (window.innerWidth >= 768) {
        if (this.cardUpdated === true) {
          clearInterval(this.intervalId)
          this.cardUpdated = false
          this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 3)
          this.intervalId = setInterval(() => {
            this.startIndex++
            if (this.startIndex === 3) {
              this.startIndex = 0
            }
            this.visibleArray = this.testimonials.slice(this.startIndex, this.startIndex + 3)
          }, 5000);
        }
      }
    },
    showModal(imageUrl) {
      this.selectedTestimonial = imageUrl
    },
    closeModal() {
      this.selectedTestimonial = null
    }
  }
}

</script>
