<template>
  <div ref="notFoundContainer" class="not-found-container">
    <p class="not-found-title">404 Page Not Found</p>
  </div>
</template>

<script>

export default {
  name: 'NotFoundPage',
  mounted() {
    this.setInitialHeight()
  },
  methods: {
    setInitialHeight() {
      const originalHeight = window.innerHeight + 'px'
      const notFoundContainer = this.$refs.notFoundContainer

      notFoundContainer.style.minHeight = originalHeight
      notFoundContainer.style.maxHeight = originalHeight
    }
  }
}

</script>

<style>
.not-found-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  background-color: whitesmoke;
  align-items: center;
  justify-content: center;
}
.not-found-title {
  font-size: 42px;
}
</style>
