<template>
  <header>
  </header>
  <main>
    <div class="app-container">
      <HeaderPage v-if="!is404Page"/>
      <router-view/>
<!--      <FooterPage v-if="!is404Page"/>-->
    </div>
  </main>
</template>

<script>
import HeaderPage from "@/components/global/header/HeaderGlobal.vue";
// import FooterPage from "@/components/global/footer/FooterPage.vue";

export default {
  name: 'App',
  components: {
    HeaderPage,
    // FooterPage,
  },
  data() {
    return{
      is404Page: false,
    }
  },
  mounted() {
    this.scrollToTop();
    this.checkIf404()
  },
  watch: {
    $route(to) {
      this.checkIf404(to)
    }
  },
  methods: {
    checkIf404(route = this.$route) {
      this.is404Page = route.path === '/404';
    },
    scrollToTop() {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    }
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  cursor: default;
}

.app-container {
  width: 100%;
  height: auto;
  overflow-x: hidden;
  font-family: "Bebas Neue", sans-serif;
  font-weight: 400;
  font-style: normal;
  background-color: black;
}
</style>
