<template>
  <div class="home-header">
    <p v-if="!scrolled" class="home-header-text">About Us</p>
    <p v-if="!scrolled" class="home-header-text">Product List</p>
    <p v-if="!scrolled" class="home-header-text">Social Media</p>
    <p v-if="!scrolled" class="home-header-text">Contact Us</p>
    <div v-else>
      <img
          v-if="!showNavigator"
          src="https://i.imgur.com/f4m7uMp.png"
          alt="Home Hamburger"
          class="home-header-hamburger"
          @click="handleNavigator"
      />
      <img
          src="https://i.imgur.com/oLjGjYp.png"
          alt="Home Logo"
          class="home-header-logo"
          @click="scrollToTop"
      />
      <div v-if="showNavigator" class="home-header-left-navigator-container" @click.self="handleNavigator">
        <div class="home-header-left-navigator">
          <p class="home-header-navigator-content">About Us</p>
          <p class="home-header-navigator-content">Product List</p>
          <p class="home-header-navigator-content">Social Media</p>
          <p class="home-header-navigator-content">Contact Us</p>
          <div class="home-header-navigator-image-container">
            <img
                src="https://i.imgur.com/L5uXCN7.png"
                class="home-header-navigator-image"
                alt="nav-image"/>
          </div>
          <div class="home-header-navigator-closer" @click="handleNavigator"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'HeaderPage',
  props: {},
  data() {
    return {
      scrolled: false,
      showNavigator: false
    };
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    document.body.style.overflow = ''
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      this.scrolled = window.scrollY > 20
      if (window.scrollY === 0) {
        this.showNavigator = false
      }
    },
    scrollToTop() {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    },
    handleNavigator() {
      this.showNavigator = !this.showNavigator
      if (this.showNavigator === true) {
        document.body.style.overflow = 'hidden'
      } else {
        document.body.style.overflow = ''
      }
    },
  }
}
</script>
