<template>
  <div class="promotionModalContainer" @click.self="close">
    <div class="promotionModalContent">
      <img class="promotionModalImage" :src="imageUrl" alt="Promotion Image">
    </div>
    <button class="promotionModalCloser" @click="close"></button>
  </div>
</template>

<script>
export default {
  name: 'ImageModal',
  props: {
    imageUrl: {
      type: String,
      required: true
    }
  },
  methods: {
    close() {
      this.$emit('closeModal');
      document.body.style.overflow = ''; // Re-enable background scrolling
    }
  },
  mounted() {
    document.body.style.overflow = 'hidden'; // Disable background scrolling
  },
  beforeUnmount() {
    document.body.style.overflow = ''; // Re-enable background scrolling when component is destroyed
  }
}
</script>

<style>
.promotionModalContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.promotionModalContent {
  background: transparent;
  width: 95%;
  height: auto;
}

.promotionModalImage {
  width: 100%;
  height: auto;
}

.promotionModalCloser {
  background-image: url("https://i.imgur.com/b2qIK8T.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-radius: 20px;
  width: 30px;
  height: 30px;
}
</style>
