<template>
  <div ref="promotionContainer" class="promotion-container">
    <p class="promotion-title">Our Promotions</p>
    <div class="card-container">
      <div v-if="shouldShowLeftArrow" class="promotion-prev-button" @click="showPrevPromotionCards"></div>
      <img v-for="(card, index) in visibleArray" :key="index" :src="card.data.imageUrl" alt="p1" class="promotion-card" @click="showModal(card.data.imageUrl)">
      <div v-if="shouldShowRightArrow" class="promotion-next-button" @click="showNextPromotionCards"></div>
    </div>
  </div>
  <ImageModal
    v-if="this.selectedPromotion"
    :image-url="selectedPromotion"
    @closeModal="closeModal"
  />
</template>

<script>

import ImageModal from "@/components/modal/image_modal/ImageModal.vue";

function promotionLists(imageUrl) {
  return {
    data : {
      imageUrl: imageUrl,
    }
  }
}export default {
  name: 'PromotionPage',
  props: {
  },
  components: {
    ImageModal,
  },
  data() {
    return {
      showNavigator: false,
      selectedPromotion: null,
      promotions: [
          promotionLists('https://i.imgur.com/H73lA7U.png'),
          promotionLists('https://i.imgur.com/QqaktR6.png'),
          promotionLists('https://i.imgur.com/X7CTGeN.png')
      ],
      visibleArray: [],
      startIndex: 0,
      intervalId: null,
      shouldShowRightArrow: false,
      shouldShowLeftArrow: false,
      cardUpdated: false
    };
  },
  mounted() {
    if (window.innerWidth < 768) {
      this.cardUpdated = true
      this.shouldShowLeftArrow = true
      this.shouldShowRightArrow = true
      this.visibleArray = this.promotions.slice(this.startIndex, this.startIndex + 1)
      this.intervalId = setInterval(() => {
        this.startIndex = (this.startIndex + 1) % this.promotions.length
        this.visibleArray = this.promotions.slice(this.startIndex, this.startIndex + 1)
      }, 5000);
    } else {
      this.visibleArray = this.promotions.slice(this.startIndex, this.promotions.length)
    }
    this.setInitialHeight()
    window.addEventListener('resize', this.updateVisibleArray);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.updateVisibleArray);
  },
  methods: {
    setInitialHeight() {
      const originalHeight = window.innerHeight + 'px'
      const promotionContainer = this.$refs.promotionContainer

      promotionContainer.style.maxHeight = originalHeight
    },
    closeModal() {
      this.selectedPromotion = null
    },
    updateVisibleArray() {
      if (window.innerWidth < 768 && this.cardUpdated === false) {
        this.cardUpdated = true
        this.shouldShowLeftArrow = true
        this.shouldShowRightArrow = true
        clearInterval(this.intervalId);
        this.visibleArray = this.promotions.slice(this.startIndex, this.startIndex + 1)
        this.intervalId = setInterval(() => {
          this.startIndex = (this.startIndex + 1) % this.promotions.length
          this.visibleArray = this.promotions.slice(this.startIndex, this.startIndex + 1)
        }, 5000);
      } else if (window.innerWidth >= 768 && this.cardUpdated === true) {
        if (this.cardUpdated) {
          this.cardUpdated = false
          this.shouldShowLeftArrow = false
          this.shouldShowRightArrow = false
          clearInterval(this.intervalId);
          this.startIndex = 0
          this.visibleArray = this.promotions.slice(this.startIndex, this.promotions.length)
        }
      }
    },
    showNextPromotionCards() {
      if (window.innerWidth < 768) {
        clearInterval(this.intervalId);
        if (this.startIndex !== this.promotions.length - 1) {
          console.log(this.startIndex)
          this.startIndex++
          this.visibleArray = this.promotions.slice(this.startIndex, this.startIndex + 1)
        }
      }
    },
    showPrevPromotionCards() {
      if (window.innerWidth < 768) {
        clearInterval(this.intervalId);
        if (this.startIndex !== 0) {
          console.log(this.startIndex)
          this.startIndex--
          this.visibleArray = this.promotions.slice(this.startIndex, this.startIndex + 1)
        }
      }
    },
    showModal(imageUrl) {
      this.selectedPromotion = imageUrl
    }
  }
}
</script>

